.footer{background-color:#f60;position:fixed;z-index:999;min-height:55px;bottom:0;left:0;right:0}.footer .inner{box-sizing:border-box;margin:0 auto;padding:15px 5px}.footer a{color:hsla(0,0%,100%,.8);line-height:1.5rem;-webkit-transition:color .15s ease;transition:color .15s ease;display:inline-block;vertical-align:middle;font-weight:300;letter-spacing:.075em;margin-right:1.8em}.footer a:hover{color:#fff}.footer a.router-link-active{color:#fff;font-weight:400}.footer a:nth-child(6){margin-right:0}